import {createRouter, createWebHistory} from "vue-router";
import Home from "@/views/Home.vue";
import Login from "@/views/Login.vue";
import Signup from "@/views/Signup.vue";
import SignupAgree from "@/views/SignupAgree.vue";
import SignupInputs from "@/views/SignupInputs.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/login",
    name: "login",
    component: Login
  },
  {
    path: "/signup",
    name: "signup",
    component: Signup,
    children: [
      {
        path: "",
        name: "signupAgree",
        component: SignupAgree
      },
      {
        path: "inputs",
        name: "signupInputs",
        component: SignupInputs
      }
    ]
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

export default router;
