<template>
  <form id="signupAgree">
    <div class="item">
      <input id="selectAll" type="checkbox" @click="selectAll" v-model="checkAll">
      <label for="selectAll" />
      <div class="text">
        <span class="name">모두 동의합니다</span>
        <span class="explain">
          모두 동의는 필수 및 선택 정보에 대한 동의가 포함되어 있으며,<br>
          선택 항목에 동의를 하지 않은 경우에도 서비스를 이용할 수 있습니다
        </span>
      </div>
    </div>
    <div class="item">
      <input id="selectAgreement" type="checkbox" name="agree" v-model="agree1">
      <label for="selectAgreement" />
      <div class="text">
          <span class="name">
          <mark>(필수)</mark>
          &nbsp;KSR 이용약관 동의
        </span>
      </div>
      <div class="open-detail">
        <a>보기</a>
      </div>
    </div>
    <div class="item">
      <input id="selectPolicy" type="checkbox" name="agree" v-model="agree2">
      <label for="selectPolicy" />
      <div class="text">
        <span class="name">
          <mark>(필수)</mark>
          &nbsp;개인정보 수집 및 이용 동의
        </span>
      </div>
      <div class="open-detail">
        <a>보기</a>
      </div>
    </div>
    <div class="item">
      <input id="selectPromotion" type="checkbox" name="agree" v-model="agree3">
      <label for="selectPromotion" />
      <div class="text">
        <span class="name">
          (선택)&nbsp;프로모션 정보 수집 동의
        </span>
      </div>
      <div class="open-detail">
        <a>보기</a>
      </div>
    </div>
    <RouterLink class="submit" to="/signup/inputs">
      <button type="submit" :disabled="!agree1 || !agree2">다음</button>
    </RouterLink>
  </form>
</template>
<script setup>

import {computed, ref} from "vue";

const selectAll = () => {
  if(!agree1.value || !agree2.value || !agree3.value) {
    agree1.value = true
    agree2.value = true
    agree3.value = true
  }else {
    agree1.value = false
    agree2.value = false
    agree3.value = false
  }
}

const agree1 = ref(false)
const agree2 = ref(false)
const agree3 = ref(false)

const checkAll = computed(() => agree1.value && agree2.value && agree3.value)
</script>
