    <swiper class="main"
            :effect="'fade'"
            :modules="modules">
      <swiper-slide>
        <img src="@/assets/images/banner/banner_01.jpg">
        <div class="text"></div>
      </swiper-slide>
    </swiper>
  </section>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import { EffectFade } from "swiper/modules";
import "swiper/css";

export default {
  components: {
    Swiper,
    SwiperSlide
  },
  setup() {
    return {
      modules: [EffectFade]
    }
  }
