<template>
  <main id="signup">
    <div class="signup-inner">
      <div class="title">
        <Logo />
        <div class="text" v-html="comment" />
      </div>
      <RouterView />
    </div>
  </main>
</template>

<script setup>
import Logo from "@/components/Logo.vue";

import { computed } from "vue";
import {useRoute} from "vue-router";

const comment = computed(() => {
  if (useRoute().name === "signupAgree") {
    return "<span>회원가입을 위해<br>아래 약관에 동의해주세요</span>";
  } else {
    return "<span>회원가입에 필요한 정보를<br>입력해주세요</span>";
  }
})
</script>

<style scoped>
@import "@/styles/Signup.css";
</style>

